import React from "react";
import { Avatar, Grid, List, ListItem, ListItemAvatar, ListItemText, makeStyles } from "@material-ui/core";
import FavoriteIcon from "@material-ui/icons/Favorite";
import SecurityIcon from "@material-ui/icons/Security";
import FastForwardIcon from "@material-ui/icons/FastForward";
import FlashOnIcon from "@material-ui/icons/FlashOn";

import CalculatedStats from "../models/CalculatedStats";


const useStyles = makeStyles({
    root: {
        width: "100%",
        maxWidth: 360,
        //backgroundColor: theme.palette.background.paper,
    },
    statisticsContainer: {},
});

type CalculatedStatisticsBlockProps = {
    statistics: CalculatedStats;
};

export default function CalculatedStatisticsBlock(props: CalculatedStatisticsBlockProps) {
    const classes = useStyles();

    const { statistics } = props;

    return (
        <Grid container>
            <Grid item xs={6}>
                <List className={classes.root}>
                    <ListItem>
                        <ListItemAvatar>
                            <Avatar>
                                <FavoriteIcon />
                            </Avatar>
                        </ListItemAvatar>
                        <ListItemText
                            primary={`Hit Points ${statistics.currentHitPoints} / ${statistics.currentHitPoints}`}
                            secondary={`Hit Die D${statistics.hitDie}`}
                        />
                    </ListItem>
                    <ListItem>
                        <ListItemAvatar>
                            <Avatar>
                                <SecurityIcon />
                            </Avatar>
                        </ListItemAvatar>
                        <ListItemText
                            primary={`Armor Class ${statistics.armour}`}
                            secondary={`Spell Resistance ${statistics.spellResistance}`}
                        />
                    </ListItem>
                </List>
            </Grid>
            <Grid item xs={6}>
                <List className={classes.root}>
                    <ListItem>
                        <ListItemAvatar>
                            <Avatar>
                                <FastForwardIcon />
                            </Avatar>
                        </ListItemAvatar>
                        <ListItemText primary={`Attack Speed ${statistics.attackSpeed}`} secondary={`Move Speed ${statistics.moveSpeed}`} />
                    </ListItem>
                    <ListItem>
                        <ListItemAvatar>
                            <Avatar>
                                <FlashOnIcon />
                            </Avatar>
                        </ListItemAvatar>
                        <ListItemText
                            primary={`Attack +${statistics.attack}`}
                            secondary={`Damage d${statistics.damageDie} + ${statistics.damageBonus}`}
                        />
                    </ListItem>
                </List>
            </Grid>
            <p>Witch Taint {statistics.taint}</p>
        </Grid>
    );
}
